import React from "react";
import Accordion from "./Accordion";

class ResponseMessage extends React.Component {
  constructor(props) {
    super(props);

    this.state = { groupControl: { identifier: "", group: "" } };
  }

  groupFn = o => {
    this.setState({ groupControl: { identifier: o.myID, group: o.groupID } });
  };

  callbackFn = bool => {
    // Do some side effect
    console.log("callbackFn(" + bool + ")");
  };

  render() {
    return (
      <div>
          <a  id="anchor"   ></a>
        {this.props.message !== "" && (
          <Accordion
            ID="accord"
            groupID="AAA"
            actionID="anchor"
            groupFn={this.groupFn}
            controler={this.state.groupControl}
            callbackFn={this.callbackFn}
          >
            <div>
              <h1>{this.props.message.title}</h1>
              <p>Name: {this.props.message.name}</p>
              <p>Hotel: {this.props.message.hotel}</p>
              <p>Arrival Date: {this.props.message.arrivalDate}</p>
              <p>Departure Date: {this.props.message.departureDate}</p>
              <pre>{JSON.stringify(this.props.message)}</pre>
            </div>
          </Accordion>
        )}
        <style jsx>{`
          pre,
          h1 {
            font-family: "Arial";
            padding: 20;
            color: #39f;
          }
        `}</style>
      </div>
    );
  }
}
export default ResponseMessage;
